body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.projects-grid {
  display: flex;
}
.home {
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  margin: -100px;
  width: 100%;
  height: 150%;
}


.resume-right-col {
  background: #27221F;
  color: white;
  padding: 2em;
}
.left-col{
  width:30%;
  height: 500px;
}


.portrait {
  height: 350px;
  position: relative;
  padding-top: 5em;
  /* -webkit-clip-path: circle(0px at 25% 25%); */
  -webkit-clip-path: circle(150px at 50% 60%);
          clip-path: circle(150px at 50% 60%)
}

.about {
  background-color: black;
  height:50%;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.about h1 {
  font-size: 40px;
  font-weight: bold;
  color: yellow;
}
.about h5 {
  font-size: 40px;
  font-weight: bold;
  color: yellow;
}

.about hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.about p {
  color: white;
  font-size: 20px;
  padding: 1em;
}
